import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import SelectSearch from 'react-select-search';
import axios from 'axios';
const InfoText = ({ data, className }) => {
  return <p className={className}>{data}</p>;
};

const infoText = `Welcome to Cargo. The only place that will let you know what's value of your car.`;
const infoText2 = `To continue, Tell us more about your car. Don't worry! It wont take long.`;

const numberWithCommas = x => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
};

const getYearsObject = (start, end) => {
  let res = [];
  for (let i = start; i <= end; i++) {
    res.push({ name: i, value: i });
  }
  res.reverse();
  return res;
};

const getMonths = () => {
  let months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];

  return months.map(each => {
    return {
      name: each,
      value: each,
    };
  });
};

const getInFormat = arr => {
  return arr.map(each => {
    return {
      name: each,
      value: each,
    };
  });
};

const vehicleTypes = [
  'coupe',
  'suv',
  'kleinwagen',
  'limousine',
  'cabrio',
  'bus',
  'kombi',
  'not-declared',
  'andere',
];
const brandArray = [
  'audi',
  'jeep',
  'volkswagen',
  'skoda',
  'bmw',
  'peugeot',
  'ford',
  'mazda',
  'nissan',
  'renault',
  'mercedes_benz',
  'seat',
  'honda',
  'fiat',
  'opel',
  'mini',
  'smart',
  'hyundai',
  'alfa_romeo',
  'subaru',
  'volvo',
  'mitsubishi',
  'kia',
  'suzuki',
  'lancia',
  'porsche',
  'citroen',
  'toyota',
  'chevrolet',
  'dacia',
  'daihatsu',
  'chrysler',
  'sonstige_autos',
  'jaguar',
  'daewoo',
  'rover',
  'saab',
  'land_rover',
  'trabant',
  'lada',
];

const fuelType = [
  'Diesel',
  'Petrol',
  'Dont know',
  'LPG',
  'Others',
  'Hybrid',
  'CNG',
  'Electric',
];

const optionsForSearch = {
  registrationYear: getYearsObject(2003, 2018),
  months: getMonths(),
  brand: getInFormat(brandArray),
  vehicleTypes: getInFormat(vehicleTypes),
  repairs: getInFormat(['Yes', 'No', 'Dont know']),
  gears: getInFormat(['Automatic', 'Manual', 'Dont know']),
  fuelType: getInFormat(fuelType),
};

class Form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      power: '',
      gears: '',
      registrationMonth: '',
      registrationYear: '',
      brand: '',
      repairs: '',
      fuelType: '',
      kilometers: '',
      loading: false,
    };
    this.onSubmitPressed = this.onSubmitPressed.bind(this);
  }

  async onSubmitPressed() {
    alert('submit form');
    let flag = false;
    const {
      power,
      gears,
      registrationMonth,
      registrationYear,
      brand,
      vehicleType,
      repairs,
      fuelType,
      kilometers,
    } = this.state;
    if (
      !power ||
      !gears ||
      !registrationMonth ||
      !registrationYear ||
      !brand ||
      !vehicleType ||
      !repairs ||
      !fuelType ||
      !kilometers
    ) {
      alert('Values Error: Please provide all the values.');
    } else {
      flag = true;
    }

    if (flag) {
      //process
      alert('safe to proceed!');
      this.setState({ loading: true });
      // data to server

      let result = await axios.post('http://127.0.0.1:4500/predict', {
        power: power,
        gears: gears.value,
        vehicleType: vehicleType.value,
        registrationMonth: registrationMonth.value,
        registrationYear: registrationYear.value,
        brand: brand.value,
        repairs: repairs.value,
        fuelType: fuelType.value,
        kilometers: kilometers,
      });

      this.setState({ result, loading: false });
      let resultString = `Your car's price is estimated to be : ${numberWithCommas(
        parseInt(result.data.price),
      )} Rupees`;
      console.log(numberWithCommas(parseInt(result.data.price)));
      alert(resultString);
      console.log(result);
    } else {
      return;
    }
  }

  render() {
    if (this.state.loading) {
      return (
        <div className="loadingContainer">
          <img src={require('./images/loading.gif')} width="400" height="400" />
          <p>Just a Moment...</p>
        </div>
      );
    } else {
      return (
        <div className="root">
          <h1 className="title">CarGo</h1>
          <InfoText className="infoText1" data={infoText} />
          <InfoText className="infoText2" data={infoText2} />
          <div className="formRoot">
            <form
              onSubmit={() => this.onSubmitPressed()}
              action="javascript:void(0);"
            >
              <div className="formRow">
                <input
                  type="text"
                  className="input"
                  size={50}
                  placeholder="Power (Numbers Only)"
                  required
                  onChange={e => this.setState({ power: e.target.value })}
                />
              </div>
              <div className="formRow">
                <input
                  type="text"
                  className="input"
                  size={50}
                  required
                  placeholder="How many kilometers car has run?"
                  onChange={e => this.setState({ kilometers: e.target.value })}
                />
              </div>
              <div className="formRow">
                <SelectSearch
                  options={this.props.optionsForSearch.registrationYear}
                  placeholder="Year of Registration"
                  className="select-search-box"
                  value="sv"
                  required
                  onChange={value => {
                    this.setState({ registrationYear: value });
                  }}
                />
                <div className="space" />
                <SelectSearch
                  options={this.props.optionsForSearch.months}
                  placeholder="Month of Registration"
                  className="select-search-box"
                  value="sv"
                  required
                  onChange={value => {
                    this.setState({ registrationMonth: value });
                  }}
                />
              </div>
              <div className="formRow">
                <SelectSearch
                  options={this.props.optionsForSearch.brand}
                  placeholder="Brand of Car"
                  className="select-search-box"
                  value="sv"
                  required
                  onChange={value => {
                    this.setState({ brand: value });
                  }}
                />
                <div className="space" />
                <SelectSearch
                  options={this.props.optionsForSearch.vehicleTypes}
                  placeholder="Type of Vehicle"
                  className="select-search-box"
                  value="sv"
                  required
                  onChange={value => {
                    this.setState({ vehicleType: value });
                  }}
                />
              </div>
              <div className="formRow">
                <SelectSearch
                  options={this.props.optionsForSearch.repairs}
                  placeholder="Does the car need Repair?"
                  className="select-search-box"
                  value="sv"
                  required
                  onChange={value => {
                    this.setState({ repairs: value });
                  }}
                />
                <div className="space" />
                <SelectSearch
                  options={this.props.optionsForSearch.gears}
                  placeholder="Gearing System"
                  className="select-search-box"
                  value="sv"
                  required
                  onChange={value => {
                    this.setState({ gears: value });
                  }}
                />
                <div className="space" />
                <div className="formRow">
                  <SelectSearch
                    options={this.props.optionsForSearch.fuelType}
                    placeholder="Fuel?"
                    className="select-search-box"
                    value="sv"
                    required
                    onChange={value => {
                      this.setState({ fuelType: value });
                    }}
                  />
                </div>
              </div>
              <div className="formRow">
                <div className="spacing" />
                <div className="spacing" />
                <input
                  type={'submit'}
                  className={'submitFormButton'}
                  value="Show Me"
                />
              </div>
            </form>
          </div>
        </div>
      );
    }
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      data: {
        infoText: infoText,
      },
    };
  }

  render() {
    return <Form optionsForSearch={optionsForSearch} />;
  }
}

export default App;



